      range: {},
      firmList: {},
      pagelist: {},
      isLoading: false
    }
  },
  created () {
    this.getData()
    $('html, body').animate({
      scrollTop: $('#app').offset().top
    }, 0)
  },
  components: { page, loading: VueLoading },
  methods: {
    getData: function (page = 1, cat = true, dog = false, other = true) {
      this.isLoading = true
      const vm = this
      const config = {
        method: 'get',
        url: `http://pettrip.rocket-coding.com/api/Room/GetCompanys?page=${page}&paged=6`
      }
      this.$http(config)
        .then(function (response) {
          console.log(response)
          vm.firmList = response.data.companies
          vm.pagelist = response.data.meta
          vm.isLoading = false
        })
        .catch(function (error) {
          console.log(error)
          vm.isLoading = false
        })
    }
  }
}
